n\tfont-size: 55px;\r\n}\r\n\r\nh2 {\r\n\tfont-size: 40px;\r\n}\r\n\r\nh3 {\r\n\tfont-size: 32px;\r\n}\r\n\r\nh4 {\r\n\tfont-size:24px;\r\n}\r\n\r\nh5 {\r\n\tfont-size: 22px;\r\n}\r\n\r\nh6 {\r\n\tfont-size: 20px;\r\n}\r\n\r\np {\r\n\tfont-size:16px;\r\n\tline-height:30px;\r\n\tmargin-bottom:15px;\r\n}\r\n\r\na {\r\n\ttransition-duration:0.5s;\r\n\t -moz-transition-duration:0.5s;\r\n\t  -o-transition-duration:0.5s;\r\n\t   -webkit-transition-duration:0.5s;\r\n\t    -ms-transition-duration:0.5s;\r\n\ttext-decoration:none;\r\n\tcolor:#38ccff;\r\n}\r\n\r\na:hover, a:focus {\r\n\ttext-decoration:none;\r\n\toutline:none;\r\n\tcolor:#03aee9;\r\n}\r\n\r\nul, ol {\r\n\tmargin:0 auto 20px;\r\n}\r\n\r\nul li, ol li {\r\n  font-size: 16px;\r\n  line-height:30px;\r\n  margin: 0 auto 10px;\r\n}\r\n\r\nul li i, ol li i {\r\n\tmargin:0 12px 0 0;\r\n}\r\n\r\nul.list_style_none, ol.list_style_none {\r\n\tlist-style:none;\r\n}\r\n\r\nul.list-with-icon {\r\n\tpadding-left:10px;\r\n\tmargin-bottom:30px;\r\n}\r\n\r\nul.list-with-icon li {\r\n  line-height: 20px;\r\n  margin-bottom: 15px;\r\n  padding-left: 23px;\r\n  position: relative;\r\n  list-style:none;\r\n}\r\n\r\nul.list-with-icon li i {\r\n  left: 0;\r\n  position: absolute;\r\n  top: 4px;\r\n}\r\n\r\nblockquote {\r\n\tbackground:#f5f5f5 ;\t\r\n}\r\n\r\nimg {\r\n\tmax-width:100%;\r\n}\r\n\r\n.white-text, \r\n.white-text h1, \r\n.white-text h2, \r\n.white-text h3, \r\n.white-text h4, \r\n.white-text h5, \r\n.white-text h6, \r\n.white-text p {\r\n\tcolor:#fff;\t\r\n}\r\n\r\n.uppercase {\r\n\ttext-transform:uppercase;\r\n}\r\n\r\n.underline {\r\n\ttext-decoration:underline;\r\n}\r\n\r\n.divider {\r\n  border-bottom: 1px solid #dcd9d9;\r\n  clear: both;\r\n  margin: 40px auto;\r\n}\r\n\r\n.divider.double {\r\n  border-bottom: 3px double #dcd9d9;\r\n}\r\n\r\n/*-----------------\r\n\t1.2. Button\r\n-------------------------*/\r\n\r\n.btn {\r\n  background: #38ccff none repeat scroll 0 0;\r\n  border: medium none;\r\n  border-radius: 3px;\r\n  color: #ffffff;\r\n  font-size: 16px;\r\n  font-weight:700;\r\n  line-height: 30px;\r\n  margin: auto;\r\n  width:auto;\r\n  padding: 8px 22px;\r\n    transition: all 0.3s linear 0s;\r\n   -moz-transition: all 0.3s linear 0s;\r\n    -o-transition: all 0.3s linear 0s;\r\n     -webkit-transition: all 0.3s linear 0s;\r\n      -ms-transition: all 0.3s linear 0s;\r\n}\r\n\r\n.outline-btn {\r\n\tborder:#38ccff solid 2px;\r\n\tbackground:none;\r\n\tcolor:#38ccff;\r\n}\r\n\r\n.btn-sm {\r\n  border-radius: 3px;\r\n  font-size: 12px;\r\n  line-height: 1.5;\r\n  padding: 5px 10px;\r\n}\r\n\r\n.btn-link {\r\n\tcolor:#38ccff;\r\n}\r\n\r\n.btn:hover, .btn:focus {\r\n\tbackground:#03aee9;\r\n\tcolor:#fff;\r\n}\r\n\r\n.btn-link:focus, .btn-link:hover {\r\n\tcolor:#03aee9;\r\n}\r\n\r\n/*-------------------------\r\n\t1.3. dark-overlay\r\n---------------------------------*/\r\n\r\n.div_zindex {\r\n  position: relative;\r\n  z-index: 1;\r\n}\r\n\r\n.dark-overlay {\r\n  background: rgba(0, 0, 0, 0.7) none repeat scroll 0 0;\r\n  content: \"\";\r\n  height: 100%;\r\n  left: 0;\r\n  position: absolute;\r\n  top: 0;\r\n  width: 100%;\r\n}\r\n\r\n/*-------------------------------------\r\n\t1.4. Space margins and padding\r\n------------------------------------------------*/\r\n\r\n.padding_none {\r\n\tpadding:0px;\r\n}\r\n\r\n.padding {\r\n\tpadding:20px 0;\r\n}\r\n\r\n.padding_50px {\r\n\tpadding:50px 0;\r\n}\r\n\r\n.padding_4x4_30 {\r\n\tpadding:30px;\t\r\n}\r\n\r\n.padding_4x4_40 {\r\n\tpadding:40px;\t\r\n}\r\n\r\n.space-20 {\r\n    width:100%;\r\n    height: 20px;\r\n\tclear:both;\r\n}\r\n\r\n.space-30 {\r\n    width:100%;\r\n    height: 30px;\r\n\tclear:both;\r\n}\r\n\r\n.space-40 {\r\n    width:100%;\r\n    height: 40px;\r\n\tclear:both;\r\n}\r\n\r\n.space-60 {\r\n    width:100%;\r\n    height: 60px;\r\n\tclear:both;\r\n}\r\n\r\n.space-80 {\r\n    height: 80px;\r\n    width:100%;\r\n}\r\n\r\n.margin-btm-20 {\r\n    margin-bottom: 20px;\r\n\tclear:both;\r\n}\r\n\r\n.margin-none {\r\n\tmargin:0px;\r\n}\r\n\r\n/*-----------------\r\n\t1.5. Form\r\n---------------------------*/\r\n\r\n.form-group {\r\n  margin-bottom: 15px;\r\n  position: relative;\r\n}\r\n\r\n.form-label {\r\n  clear: both;\r\n  display: block;\r\n  font-size: 15px;\r\n  font-weight: 800;\r\n  margin: 0 auto 8px;\r\n}\r\n\r\n.form-control, form input, form textarea, form select {\r\n  background: #eeeeee none repeat scroll 0 0;\r\n  border: 0 none;\r\n  border-radius: 3px;\r\n  box-shadow: none;\r\n  color: #888888;\r\n  font-size: 14px;\r\n  height: 50px;\r\n  line-height: 30px;\r\n  padding: 0 20px;\r\n  width:100%;\r\n}\r\n\r\n.form-control:focus, \r\nform input:focus, \r\nform textarea:focus, \r\nform select:focus {\r\n  box-shadow:none;\r\n  outline:#38ccff solid 1px;\t\r\n}\r\n\r\ntextarea {\r\n\twidth:100%;\r\n\tmin-height:150px;\r\n}\r\n\r\ntextarea.form-control {\r\n\tpadding:15px 20px;\r\n}\r\n\r\n.select {\r\n\tposition:relative;\r\n}\r\n\r\n.select select {\r\n  appearance: none;\r\n   -moz-appearance: none;\r\n   -o-appearance: none;\r\n   -webkit-appearance: none;\r\n   -ms-appearance: none;\r\n  \r\n}\r\n\r\n.select::after {\r\n  color: #878787;\r\n  content: \"\";\r\n  cursor: pointer;\r\n  font-family: fontawesome;\r\n  font-size: 15px;\r\n  padding: 12px 0;\r\n  pointer-events: none;\r\n  position: absolute;\r\n  right: 15px;\r\n  top: 0;\r\n}\r\n\r\n.form-control option {\r\n  padding: 10px;\r\n}\r\n\r\n.control-label {\r\n\tcolor:#555;\r\n\tfont-size:15px;\r\n\tfont-weight:700;\t\r\n}\r\n\r\n.checkbox, .radio {\r\n  margin-bottom: 15px;\r\n  margin-top:0px;\r\n}\r\n\r\n.radio label, .checkbox label {\r\n  cursor: pointer;\r\n  font-size: 14px;\r\n  font-weight: 400;\r\n  padding-left: 26px;\r\n  position: relative;\r\n  line-height:22px;\r\n}\r\n\r\n.radio input[type=radio],\r\n.checkbox input[type=checkbox] {\r\n\tdisplay: none;\r\n}\r\n\r\n.radio label:before {\r\n    background-color:transparent;\r\n\tborder-style:solid;\r\n\tborder-width:1px;\r\n\tborder-radius:50%;\r\n    content: \"\";\r\n    display: inline-block;\r\n    height: 15px;\r\n    left: 0;\r\n\ttop:4px;\r\n    position: absolute;\r\n    width: 15px;\t\r\n}\r\n\r\n.checkbox label::before {\r\n  background-color: rgba(0, 0, 0, 0);\r\n  border: 2px solid #cccccc;\r\n  content: \"\";\r\n  display: inline-block;\r\n  height: 16px;\r\n  left: 0;\r\n  position: absolute;\r\n  top: 4px;\r\n  width: 16px;\r\n}\r\n\r\n.radio input[type=radio]:checked + label:before {\r\n\tcontent: \"\\2022\";\r\n\tfont-size: 13px;\r\n\ttext-align: center;\r\n    line-height: 12px;\r\n\tfont-weight:bold;\r\n}\r\n\r\n.checkbox input[type=checkbox]:checked + label:before {\r\n\tcontent: \"\\2713\";\r\n\tfont-size: 13px;\r\n\ttext-align: center;\r\n    line-height: 12px;\r\n\tfont-weight:bold;\r\n}\r\n\r\n/*-----------------\r\n\t1.6. Table\r\n---------------------------*/\r\n\r\ntable {\r\n\tmargin:0 0 30px;\r\n\twidth:100%;\r\n}\r\n\r\ntable th, table td {\r\n  border: 1px solid #cccccc;\r\n  padding: 15px;\r\n  padding:18px;\r\n}\r\n\r\ntable th img, table td img {\r\n\tmax-width:100%;\r\n}\r\n\r\ntable thead {\r\n\tbackground:#eee;\r\n}\r\n\r\ntable thead th, table thead td {\r\n\ttext-transform:uppercase;\r\n\tfont-weight:900;\r\n\tcolor:#111;\r\n}\r\n\r\n/*-----------------\r\n\t1.7. Accordion\r\n---------------------------*/\r\n\r\n.panel.panel-default {\r\n\tmargin:0 auto 30px;\r\n}\r\n\r\n.panel-group {\r\n  padding-top: 14px;\r\n}\r\n\r\n.panel-default {\r\n  border-color: #ecebeb;\r\n}\r\n\r\n.panel-default > .panel-heading {\r\n  background-color: #fafafa;\r\n  border-color: #ecebeb;\r\n}\r\n\r\n.panel-default > .panel-heading + .panel-collapse > .panel-body {\r\n  border-top-color: #ecebeb;\r\n}\r\n\r\n.panel-group .panel {\r\n  border-radius: 0;\r\n}\r\n\r\n.panel-group .panel .panel-title {\r\n  font-size: 20px;\r\n  font-weight: 400;\r\n}\r\n\r\n.panel-group .panel .panel-title a.collapsed {\r\n\tcolor:#111;\r\n}\r\n\r\n.panel-group .panel .panel-title a {\r\n\tpadding:10px;\r\n\tdisplay:block;\r\n}\r\n\r\n.panel-group .panel .panel-title a:hover, \r\n.panel-group .panel .panel-title a {\r\n\tcolor:#111;\r\n\tposition:relative;\r\n\tpadding-right:25px;\r\n}\r\n\r\n.panel-group .panel .panel-title a:after {\r\n\tposition:absolute;\r\n\ttop:10px;\r\n\tright:10px;\r\n\tfont-family:fontawesome;\r\n\tcontent:\"\\f107\";\t\r\n}\r\n\r\n.panel-group .panel .panel-title a.collapsed:after {\r\n\tcontent:\"\\f105\";\t\r\n}\r\n\r\n.panel-group .panel .panel-title a.collapsed {\r\n\tcolor:#111;\r\n}\r\n\r\n.panel-collapse .panel-body {\r\n\tpadding:25px;\r\n}\r\n\r\n/*-----------------------------------------------------------\r\n\t1.8. Section-background-color & sectino-heading\r\n-------------------------------------------------------------------*/\r\n\r\n.secondary-bg {\r\n\tbackground-color:#111111 !important;\r\n\tcolor:#fff;\r\n}\r\n\r\n.primary-bg {\r\n\tbackground-color:#38ccff;\r\n}\r\n\r\n.gray_bg {\r\n\tbackground:#f0f2f2;\r\n}\r\n\r\n.section-padding {\r\n  padding: 80px 0;\r\n  position:relative;\r\n}\r\n\r\n.section-header {\r\n  clear: both;\r\n  float: none !important;\r\n  margin: 0 auto;\r\n  max-width: 850px;\r\n  padding-bottom: 60px;\r\n}\r\n\r\n.section-header p {\r\n  font-size: 18px;\r\n  margin: 20px auto 0;\r\n}\r\n\r\n/*--------------------------------------------\r\n\t1.9. parallex-background\r\n---------------------------------------------------*/\r\n\r\n.parallex-bg {\r\n  background-attachment: fixed;\r\n  background-position: center;\r\n  background-repeat: no-repeat;\r\n  background-size: cover;\r\n  position: relative;\r\n}\r\n\r\n/*------------------------\r\n\t1.10. Modal\r\n--------------------------------*/\r\n\r\n.modal-dialog {\r\n  max-width: 500px;\r\n}\r\n\r\n.modal-content {\r\n  padding: 0 32px 22px;\r\n}\r\n\r\n.modal-header {\r\n\tpadding:15px 0;\r\n\tmargin-bottom:15px;\r\n\tdisplay: block;\r\n}\r\n\r\n.modal-header p {\r\n\tmargin:0 auto;\r\n}\r\n\r\n.modal-body {\r\n\tpadding:10px 0;\